.MainDiv{
    width: 100%;
    display: flex;
    background-color: #e95858;
}


.sapLogo{
    margin-top: 15px;
    margin-left: 20px;
    height:150px;
    width: 150px;;
}

.Predict{
    margin-top: 80px;
    margin-left: 20px;
    width: 175px;
    height: 70px;
    font-size: 35px;
    border-radius: 10px;
    border-color: #64d8d8;
    background-color: #c24f4f;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    box-shadow: 0px;
    border-style:double;

}


.Predict:hover{
    background-color:#000;
    color: #fff;
    border-color: #000;
    transform: scaleX(1.1111111113333333333);
    transition: 600ms ease-in-out
}

